import React, { Component } from 'react'

const _popconfirm = require('antd/lib/popconfirm')
const _modal = require('antd/lib/modal')

import UserSetTag from './UserSetTag'

import CSSModules from 'react-css-modules'
import styles from './style.css'

// eslint-disable-next-line react/no-multi-comp
class SelectorState extends Component {
  constructor(props) {
    super(props)
    this.state = {
      visible: false
    }
    this.showModal = this.showModal.bind(this)
    this.hideModal = this.hideModal.bind(this)
    this.handleDelete = this.handleDelete.bind(this)
    this.handleClear = this.handleClear.bind(this)
  }
  // handle the visibility of count modal
  showModal() {
    this.setState({
      visible: true
    })
  }
  hideModal() {
    this.setState({
      visible: false
    })
  }
  handleDelete(tagConfig) {
    tagConfig.onDelete()
  }
  handleClear() {
    this.hideModal()
    this.props.onClear()
  }
  render() {
    const { tagsConfigs, showCategory } = this.props
    let tags = []
    if (showCategory) {
      tags = Object.keys(tagsConfigs).map(category => {
        const { name, tagsConfig } = tagsConfigs[category]
        if (tagsConfig.length === 0) return null
        return (
          <div key={category} style={{ paddingBottom: '15px' }}>
            <div className="fs12" style={{ padding: '0 5px 5px' }}>{name}</div>
            <div>
              {
                tagsConfig.map((config) => (
                  <UserSetTag
                    key={`${config.key ? config.key : config.label}`}
                    config={config}
                    onClose={this.handleDelete}
                    />
                ))
              }
            </div>
          </div>
        )
      })
    } else {
      Object.keys(tagsConfigs).forEach(category => {
        const { tagsConfig } = tagsConfigs[category]
        if (tagsConfig.length > 0) {
          tags = tags.concat(tagsConfig.map(config => (
            <UserSetTag
              key={`${config.key ? config.key : config.label}`}
              config={config}
              onClose={this.handleDelete}
              />
          )))
        }
      })
    }
    return (
      <div className="full-height" style={{ background: '#F0F6FA', padding: '35px 20px 0', overflow: 'auto' }}>
        <header style={{ paddingBottom: '10px', borderBottom: '1px solid #E9E9E9' }}>
          <div className="text-center fs16" style={{ color: '#555555' }}>已选条件</div>
          <div className="text-right fs12 pointer" style={{ color: '#666666' }} onClick={this.showModal}>
            <i className="fa fa-times" aria-hidden="true" styleName="close-button"></i>全部删除
          </div>
        </header>
        <div
          className="scrollbar"
          style={{
            height: '350px',
            paddingTop: '20px',
            overflowY: 'scroll',
            overflowX: 'hidden',
            scrollbarBaseColor: 'transparent'
          }}
          >
          {[...tags]}
        </div>
        <footer style={{ padding: '50px 22px 0', borderTop: '1px solid #E9E9E9' }}>
          {this.props.children}
        </footer>
        <_modal
          visible={this.state.visible}
          onCancel={this.hideModal}
          wrapClassName="vertical-center fixed-position"
          width="300px"
          footer={(
            <div className="text-center">
              <div className="btn-theme1" style={{ marginRight: '10px' }} onClick={this.handleClear}>确认</div>
              <div className="btn-theme2" onClick={this.hideModal}>取消</div>
            </div>
          )}
          >
          <div className="fs16 text-center">确认全部删除吗？</div>
        </_modal>
      </div>
    )
  }
}

SelectorState.propTypes = {
  tagsConfigs: React.PropTypes.object,
  showCategory: React.PropTypes.bool,
  onClear: React.PropTypes.func,
  children: React.PropTypes.element
}
SelectorState.defaultProps = {
  tagsConfigs: {},
  showCategory: false,
  onClear: (v) => (v)
}

export default CSSModules(SelectorState, styles)



