const myDataSet = {
  // 获取所有预定义画像用户集
  predefinedUserSet: 'http://data.god.pt.xiaomi.com/data/predefined',
  // 提交新的画像用户集
  userProfileSet: (user) => `http://data.god.pt.xiaomi.com/user/datas/${user}`,
  // 删除一个画像用户集
  userProfileSetDelete: 'http://data.god.pt.xiaomi.com/data/delete',
  // 获取两个画像用户集的差集/交集/合集
  userProfileOperation: 'http://data.god.pt.xiaomi.com/data/cross',
  // 重算一个画像用户集
  userProfileRecount: 'http://data.god.pt.xiaomi.com/data/copy',
  // 提交一个推广用户集
  pushPromotionSet: (user) => `http://data.god.pt.xiaomi.com/user/pushs/${user}`,
  // 删除一个推广用户集
  pushPromotionDelete: 'http://data.god.pt.xiaomi.com/push/delete',
  // 获取两个推广用户集的差集/交集/合集
  pushPromotionOperation: 'http://data.god.pt.xiaomi.com/push/cross',
  // 重算一个推广用户集
  pushPromotionRecount: 'http://data.god.pt.xiaomi.com/push/copy'
}

export default myDataSet



