import extractUserSet from './extractUserSet.js'
import myDataSet from './myDataSet.js'
import properties from './properties.js'
import report from './report.js'
import base from './base.js'
const apiMap = {
  base,
  extractUserSet,
  myDataSet,
  properties,
  report
}

const api = (path, ...args) => {
  const [key, value] = path.split(':')
  if (typeof apiMap[key][value] === 'string') {
    return apiMap[key][value]
  }
  try {
    return apiMap[key][value](...args)
  } catch (e) {
    console.error('SRC/api', path)
    console.error(e)
    return ''
  }
}

export default api



